@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,300,400,500,700,900|Noto+Sans:400,400i,700,700i&display=swap&subset=korean);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-family: 'Noto Sans', sans-serif;
font-family: 'Noto Sans KR', sans-serif; */
*{
box-sizing: border-box;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
margin: 0;
padding: 0;
border: 0;
font-size: 18px;
color: #111;
font: inherit;
vertical-align: baseline;
font-family: 'Noto Sans KR', sans-serif;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
display: block;
}
body {
line-height: 1;
}
ol, ul {
list-style: none;
}
blockquote, q {
quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
content: '';
content: none;
}
table {
border-collapse: collapse;
border-spacing: 0;
}
a{
text-decoration: none;
}
.HomeMain{
    width: 100%;
    min-height: 3000px;
    background: #1b1b1b;
    padding-top: 108px;
}
.HomeMain__mainSlide{
    height: 580px;
    width: 100%;
    background: #1b1b1b;
    position: relative;
    overflow: hidden;
}
.HomeMain__mainSlide__slideWrapper{
    position: absolute;
    width: 500%;
    left:-75%;
    display: flex;
    z-index: 10;
    transition: .35s;
}
.HomeMain__mainSlide__slideWrapper li{
    width:760px;
    height:480px;
    background: #fefefe;
    margin-right: 30px;
    position: relative;
}
.HomeMain__mainSlide__slideWrapper li img{
    position: absolute;
    top: 0;
    left:50%;
    transform: translateX(-50%);
    height: 100%;
    width: 100%;
}
.HomeMain__mainSlide .prevBtn{
    background: url(data:image/png;base64,) no-repeat center/ cover;
    outline-style: none;
    border: none;
    position: absolute;
    top: 186px; left: 100px;
    width: 100px;
    height: 100px;
    z-index: 20;
    cursor: pointer;
    -webkit-filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
            filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
}
.HomeMain__mainSlide .prevBtn:hover{
    -webkit-filter: none;
            filter: none;
}
.HomeMain__mainSlide .nextBtn{
    background: url(data:image/png;base64,) no-repeat center/ cover;
    outline-style: none;
    border: none;
    position: absolute;
    top: 186px; right: 100px;
    width: 100px;
    height: 100px;
    z-index: 20;
    cursor: pointer;
    -webkit-filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
            filter: invert(1%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
}
.HomeMain__mainSlide .nextBtn:hover{
    -webkit-filter: none;
            filter: none;
}
.HomeMain__mainSlide__indicator{
    width: 100%;
    height: 30px;
    position: absolute;
    bottom: 50px;
    left: 53%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
}
.HomeMain__mainSlide__indicator .controlBtn{
    width: 30px;
    height: 30px;
    outline-style: none;
    border: none;
    cursor: pointer;
    background: url(data:image/png;base64,) no-repeat center / cover;
}
.HomeMain__mainSlide__indicator span{
    width: 10px;
    height: 10px;
    background: #333333;
    border-radius: 100%;
    margin:0 3px;
    cursor: pointer;
}
.mainWrapper{
    width: 100%;
}
header{
    width:100%;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 99;
}
.headBar{
    width: 100%;
    background: #1b1b1b;
    border-bottom: 1px solid rgba(235,235,235,0.1);
}
.headBar ul{
    height: 33px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 140px;
}
.headBar ul li{
    color: #a5a5a5;
    font-size:12px;
    padding: 0 11px;
    cursor: pointer;
    line-height: 34px;
    position: relative;
}
.headBar ul li:after{
    content: '';
    position: absolute;
    top:12px;right: 0;
    width: 1px;
    height: 10px;
    background: #2f2f2f;
}
.headBar ul li:last-child:after{
    background:none;
}
.mainNav{
    width: 100%;
    padding: 0 140px;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #1b1b1b;
}
.mainNav h1 img{
    cursor: pointer;
    width: 111px;
}
.mainNav__category{
    width:690px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: none;
    z-index: 98;
    margin-left: 35px;
}
.mainNav__category li{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1.15rem;
    cursor: pointer;
    position: relative;
    margin: 0 18px;
    line-height: 75px;
}
.mainNav__category li:hover{
    opacity: 1;
}
.mainNav__category li.clicked{
    opacity: 1;
}
.mainNav__searchBtn{
    background: none;
    outline-style: none;
    border: none;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.mainNav__searchBtn img{
    width: 33px;
}
.mainNav__searchBtn span{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1rem;
}
.mainNav__catBtn{
    background: none;
    outline-style: none;
    border: none;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.mainNav__catBtn img{
    width: 33px;
    opacity: 0.6;
}
.mainNav__catBtn:hover img{
    opacity: 1;
}
.mainNav__catBtn span{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1rem;
}
.mainNav__catBtn:hover span{
    opacity: 1;
}

.MainContents{
    width: 100%;
    height: 3000px;
    background: #1b1b1b;
}
.MainContents h2{
    padding-top: 300px;
    text-align: center;
    font-size: 2rem;
    color: #fefefe;
}
